.auth-page {
  height: 100%;
  padding: 25px 10px;

  &__tab-content {
    &.react-tabs__tab-panel--selected {
      padding: 30px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    transform: translateY(-5%);
  }

  &__title {
    font-family: "Pacifico Regular", 'Arial', serif;
    font-weight: normal;
    text-align: center;
    font-size: 44px;
    color: #71c9ce;
    text-shadow: 1px 1px 0 rgb(140, 138, 102);
    margin-bottom: 20px;
  }

  &__form {
    margin: 0 auto;
    padding: 0;
    overflow: hidden;
  }

  .expense-details__tab {
    background: #cbf1f5;
  }

  .expense-details__tab.react-tabs__tab--selected {
    //background: #cbf1f5;
    background: none;
  }


}


