.expenses-page {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__brief {
    padding: 10px;
    background: #a6e3e9;
    text-align: center;
  }

  &__journey-title {
    font-family: "Pacifico Regular", 'Arial', serif;
    font-size: 18px;
  }

  &__expense-info {
    display: flex;
    justify-content: center;
    align-items: center;

    font-family: 'Open Sans Regular', 'Arial', sans-serif;
    text-align: left;
    font-size: 13px;
  }

  &__separator {
    margin: 0 6px;

    font-family: 'Arial', sans-serif;
    font-size: 26px;
    line-height: 10px;
  }

  &__body {
    position: relative;
  }

  &__summary-debt {
    display: flex;
    justify-content: space-between;

    font-family: 'Open Sans Regular', 'Arial', sans-serif;
    font-size: 13px;
    text-align: left;

    padding-top: 6px;

    span {
      font-family: 'Open Sans Semibold', 'Arial', sans-serif;
    }
  }

  &__header {
    display: flex;

    text-align: center;
    padding: 10px;
    background: #71c9ce;
  }

  &__body {
    flex: 1;
    overflow-y: auto;
  }

  &__select-box {
    flex: 1;

    select {
      padding: 3px 15px;
      width: 100%;
      margin: 0;
      vertical-align: middle;
      border: 1px solid;
      border-radius: 6px;

      background: #71c9ce;
      color: #e3fdfd;
      font-size: 16px;
      height: 32px;
      line-height: 1;
    }
  }

  &__add-expense {
    padding-left: 10px;

    a {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 8px;
      height: 32px;
      min-width: 0;

      font-size: 24px;
      font-family: 'Open Sans Semibold', 'Arial', sans-serif;
      line-height: 1;

      span {
        user-select: none;
      }
    }
  }

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  &__list-item {
    border-bottom: 1px solid #71c9ce;
  }

  &__item-content {
    display: flex;
    justify-content: space-between;
    padding: 5px 10px;

    font-size: 12px;
    text-decoration: none;
    color: inherit;
  }

  &__item-info {
    display: flex;
    flex-direction: column;
    flex: 1;

    overflow: hidden;
  }

  &__info-top {
    display: flex;
  }

  &__item-time {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-right: 10px;

    font-size: 12px;
  }

  &__info-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item-amount {
    font-size: 14px;
  }

  &__item-open {
    display: flex;
    align-items: center;
    padding: 0 5px 0 10px;

    font-size: 16px;
    color: #71c9ce;
  }

  &__item-title {
    font-size: 18px;
    flex: 1;
    padding-right: 5px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &__empty-info {
    text-align: center;
  }

  &__angle-arrow {
    display: inline-block;
    transform: scaleY(-1);

    position: absolute;
    top: 5px;
    right: 9px;

    line-height: 1;
    font-size: 93px;
    color: rgba(113, 201, 206, 0.2);
  }

  &__empty-notice {
    font-family: 'Open Sans Regular', 'Arial', sans-serif;
    text-align: center;
    font-size: 12px;

    span {
      display: inline-block;
      width: 20px;
      height: 20px;
      border: 1px solid;
      margin-left: 4px;
      border-radius: 5px;
      background: #71c9ce;
      position: relative;
      top: 1px;

      font-size: 16px;
      font-family: 'Open Sans Semibold', 'Arial', sans-serif;
      color: #fff;
      line-height: 18px;
      opacity: 0.4;
    }
  }
}