.expense-payers {
  &--multi {
    .expense-payers {
      &__show-multi-btn {
        background: #cbf1f5;
        font-family: 'Open Sans Semibold', 'Arial', sans-serif;
        &:before {
          content: '✓';
          padding-left: 0;
          padding-right: 6px;
        }
      }
    }
  }

  &__transition {
    position: absolute;
    top: 0;
    width: 100%;
  }

