  &__multiple-payers {
    .expense-payers__item {
      display: flex;
      width: 100%;
      padding: 10px;
      border-bottom: 1px solid #71c9ce;
      background: #e3fdfd;
      position: relative;
    }
  }

  &__show-multi-btn {
    display: flex;
    width: 100%;
    padding: 10px;
    border-width: 1px 0 1px 0;
    border-style: solid;
    border-color: #71c9ce;
    background: #e3fdfd;
    position: relative;
    &:after {
      content: '❯';
      color: #71c9ce;
      position: absolute;
      right: 10px;
    }
  }
